import {InstanceContext} from "@/context/InstanceContext";
import {$getSelection, $isRangeSelection, COMMAND_PRIORITY_EDITOR, LexicalEditor} from "lexical";
import {$insertNodeToNearestRoot} from "@lexical/utils";
import {INSERT_HORIZONTAL_RULE_COMMAND, $createHorizontalRuleNode} from "@lexical/extension";

export class HorizonalRulePlugin {

    root!: HTMLElement
    editor!: LexicalEditor
    editorId!: number

    init(instanceContext: InstanceContext) {
        console.log("Initializing horizontal rule plugin", instanceContext.wrapper);
        this.root = instanceContext.wrapper;
        this.editor = instanceContext.editor;
        this.editorId = instanceContext.fieldHash;

        this.editor.registerCommand(INSERT_HORIZONTAL_RULE_COMMAND, () => {
            this.addHorizontalRule();
            return true;
        }, COMMAND_PRIORITY_EDITOR);
    }

    addHorizontalRule() {
        this.editor.update(() => {
            const selection = $getSelection();
            if (!$isRangeSelection(selection)) {
                return false;
            }

            const focusNode = selection.focus.getNode();
            if (focusNode !== null) {
                const horizontalRuleNode = $createHorizontalRuleNode();
                $insertNodeToNearestRoot(horizontalRuleNode);
            }

            return true;
        });
    }

}

export function registerHorizontalRulePlugin(instanceContext: InstanceContext): void {
    new HorizonalRulePlugin().init(instanceContext);
}