export class ToolbarContextKey {
    static readonly SELECTION = 'isSelection';
    static readonly BLOCK_TYPE = 'blockType';
    static readonly IS_BOLD = 'isBold';
    static readonly IS_ITALIC = 'isItalic';
    static readonly IS_STRIKETHROUGH = 'isStrikethrough';
    static readonly IS_LINK = 'isLink';
}

const INITIAL_TOOLBAR_STATE = {
    //bgColor: '#fff',
    isSelection: false,
    blockType: 'paragraph' as keyof typeof blockTypeToBlockName,
    //canRedo: false,
    //canUndo: false,
    //codeLanguage: '',
    //codeTheme: '',
    //elementFormat: 'left' as ElementFormatType,
    //fontColor: '#000',
    //fontFamily: 'Arial',
    isBold: false,
    //isCode: false,
    //isHighlight: false,
    //isImageCaption: false,
    isItalic: false,
    isLink: false,
    isStrikethrough: false,
    //isSubscript: false,
    //isSuperscript: false,
    //isUnderline: false,
    //isLowercase: false,
    //isUppercase: false,
    //isCapitalize: false,
    //rootType: 'root' as keyof typeof rootTypeToRootName,
    //listStartNumber: null as number | null,
};

type ToolbarState = typeof INITIAL_TOOLBAR_STATE;
type ToolbarStore = ReturnType<typeof createSimpleStore>;

function createSimpleStore(initial: ToolbarState) {
    let state = { ...initial };

    return {
        get() {
            return state;
        },
        set(next: ToolbarState | ((prev: ToolbarState) => ToolbarState)) {
            state = typeof next === 'function' ? next(state) : next;
            return state;
        },
        update(patch: Partial<ToolbarState>) {
            state = { ...state, ...patch };
            return state;
        },
        reset(v = initial) {
            state = { ...v };
            return state;
        }
    };
}

const storesById: Map<number, ToolbarStore> = new Map();
/**
 * Zsk nebo vytvo store pro dan stringov id (nap. fieldHash).
 * Pokud m element, preferuj getToolbarStoreForElement.
 * @param {string} id
 * @returns {object} store
 */
function getToolbarStoreForId(id: number): ToolbarStore {
    if (!id) throw new Error('id is required');
    let s = storesById.get(id);
    if (!s) {
        s = createSimpleStore(INITIAL_TOOLBAR_STATE);
        storesById.set(id, s);
    }
    return s;
}

export default class ToolbarContext {

    static readonly getToolbarStoreForId = getToolbarStoreForId;
}
