import {
    DecoratorNode,
    LexicalNode,
    NodeKey,
    SerializedLexicalNode,
    Spread
} from "lexical";
import Utils from "@/Utils";
import {DELETE_IMAGE_COMMAND} from "@/plugins/ImageSelectorPlugin";

export type SerializedImageNode = Spread<{
    src: string;
    alt: string;
    type: "image";
    version: 1;
}, SerializedLexicalNode>;

export class ImageNode extends DecoratorNode<HTMLElement> {
    private readonly __src: string;
    private readonly __alt: string;

    static getType(): string {
        return "image";
    }

    static clone(node: ImageNode): ImageNode {
        return new ImageNode(node.__src, node.__alt, node.__key);
    }

    constructor(src: string, alt: string, key?: NodeKey) {
        super(key);
        this.__src = src;
        this.__alt = alt;
    }


    get getSrc(): string {
        return this.__src;
    }

    get getAlt(): string {
        return this.__alt;
    }

    createDOM(): HTMLElement {
        const wrapper = document.createElement("div");
        wrapper.className = "image-node-wrapper";

        const img = document.createElement("img");
        img.src = this.__src;
        img.alt = this.__alt;
        img.tabIndex = 0;

        const deleteIcon = document.createElement("i");
        deleteIcon.className = "image-node-delete-icon fa-solid fa-trash";
        deleteIcon.addEventListener("click", (e) => {
            e.preventDefault();
            e.stopPropagation();
            Utils.instanceContext.editor.dispatchCommand(DELETE_IMAGE_COMMAND, this.getKey());
        });

        const altTooltip = document.createElement("div");
        altTooltip.className = "image-node-alt-tooltip";
        altTooltip.textContent = this.__alt;

        wrapper.appendChild(img);
        wrapper.appendChild(deleteIcon);
        wrapper.appendChild(altTooltip);
        wrapper.tabIndex = 0;

        return wrapper;
    }

    isIsolated(): boolean {
        return true;
    }

    isKeyboardSelectable(): boolean {
        return true;
    }

    updateDOM(): false {
        return false;
    }

    static importJSON(serializedNode: SerializedImageNode): ImageNode {
        const { src, alt } = serializedNode;
        return new ImageNode(src, alt);
    }

    exportJSON(): SerializedImageNode {
        return {
            type: "image",
            version: 1,
            src: this.__src,
            alt: this.__alt,
        };
    }
}

export function $createImageNode(src: string, alt: string): ImageNode {
    return new ImageNode(src, alt);
}

export function $isImageNode(node: LexicalNode): node is ImageNode {
    return node instanceof ImageNode;
}