import {
    $addUpdateTag,
    $findMatchingParent,
    $getSelection, $isElementNode, $isNodeSelection,
    $isRangeSelection, $isRootOrShadowRoot, COMMAND_PRIORITY_CRITICAL,
    FORMAT_TEXT_COMMAND,
    LexicalEditor, LexicalNode, SELECTION_CHANGE_COMMAND,
    SKIP_SELECTION_FOCUS_TAG
} from "lexical";
import {INSERT_HORIZONTAL_RULE_COMMAND,} from '@lexical/extension';
import {
    $isCodeNode,
    normalizeCodeLanguage as normalizeCodeLanguagePrism,
} from '@lexical/code';
import {$createHeadingNode, $isHeadingNode, HeadingTagType} from "@lexical/rich-text";
import {$setBlocksType} from '@lexical/selection';
import {$isLinkNode} from '@lexical/link';
import {$isListNode, ListNode} from '@lexical/list';
import {$isTableNode, $isTableSelection} from '@lexical/table';
import ToolbarContext, {blockTypeToBlockName, ToolbarContextKey} from "@/context/ToolbarContext";
import {$getNearestNodeOfType} from "@lexical/utils";
import Utils from "@/Utils";
import {InstanceContext} from "@/context/InstanceContext";
import {FloatingLinkEditorPlugin, OPEN_LINK_MODAL_COMMAND} from "@/plugins/FloatingLinkEditorPlugin";
import {OPEN_IMAGE_SELECTOR_MODAL_COMMAND} from "@/plugins/ImageSelectorPlugin";

export class ToolbarPlugin {

    root!: HTMLElement
    editor!: LexicalEditor
    editorId!: number
    floatingLinkEditor!: FloatingLinkEditorPlugin;

    dropdownBlockType!: HTMLElement | null;
    dropdownBlockTypeButton!: HTMLElement | null;
    dropdownBlockTypeH1!: HTMLElement | null;
    dropdownBlockTypeH2!: HTMLElement | null;
    dropdownBlockTypeH3!: HTMLElement | null;
    buttonBold!: HTMLElement | null;
    buttonItalic!: HTMLElement | null;
    buttonStrikethrough!: HTMLElement | null;
    buttonLink!: HTMLButtonElement | null;
    buttonImage!: HTMLButtonElement | null;
    buttonHr!: HTMLButtonElement | null;

    init(instanceContext: InstanceContext) {
        console.log("Initializing actions plugin", instanceContext.wrapper);
        this.root = instanceContext.wrapper;
        this.editor = instanceContext.editor;
        this.editorId = instanceContext.fieldHash;
        this.floatingLinkEditor = instanceContext.floatingLinkEditor;

        instanceContext.editor.registerCommand(SELECTION_CHANGE_COMMAND,
            (_payload, newEditor) => {
                this.updateToolbar();
                return false;
            },
            COMMAND_PRIORITY_CRITICAL);

        this.loadButtons();
        this.registerEvents();
    }

    private loadButtons() {
        this.dropdownBlockType = this.root.querySelector('.jakon-editor-toolbar .block-type-dropdown');
        this.dropdownBlockTypeButton = this.root.querySelector('.jakon-editor-toolbar .block-type-dropdown button');
        this.dropdownBlockTypeH1 = this.root.querySelector('.jakon-editor-toolbar .block-type-dropdown .text-type-h1');
        this.dropdownBlockTypeH2 = this.root.querySelector('.jakon-editor-toolbar .block-type-dropdown .text-type-h2');
        this.dropdownBlockTypeH3 = this.root.querySelector('.jakon-editor-toolbar .block-type-dropdown .text-type-h3');
        this.buttonBold = this.root.querySelector('.jakon-editor-toolbar .bold');
        this.buttonItalic = this.root.querySelector('.jakon-editor-toolbar .italic');
        this.buttonStrikethrough = this.root.querySelector('.jakon-editor-toolbar .strikethrough');
        this.buttonLink = this.root.querySelector('.jakon-editor-toolbar .link');
        this.buttonImage = this.root.querySelector('.jakon-editor-toolbar .image');
        this.buttonHr = this.root.querySelector('.jakon-editor-toolbar .hr');
    }

    private registerEvents() {
        this.buttonBold?.addEventListener('click', (e) => {
            e.preventDefault();
            this.editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'bold');
        });
        this.buttonItalic?.addEventListener('click', (e) => {
            e.preventDefault();
            this.editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'italic');
        });
        this.buttonStrikethrough?.addEventListener('click', (e) => {
            e.preventDefault();
            this.editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'strikethrough');
        });
        this.buttonHr?.addEventListener('click', (e) => {
            e.preventDefault();
            this.editor.dispatchCommand(INSERT_HORIZONTAL_RULE_COMMAND, undefined);
        });

        // @ts-ignore
        this.addEvent('text-type-normal', () => this.formatHeading(this.editor, 'paragraph'));
        this.addEvent('text-type-h1', () => this.formatHeading(this.editor, 'h1'));
        this.addEvent('text-type-h2', () => this.formatHeading(this.editor, 'h2'));
        this.addEvent('text-type-h3', () => this.formatHeading(this.editor, 'h3'));


        this.buttonLink?.addEventListener('click', (e) => {
            e.preventDefault();
            const isLink = ToolbarContext.getToolbarStoreForId(this.editorId).get().isLink;
            this.editor.dispatchCommand(OPEN_LINK_MODAL_COMMAND, isLink);
        });
        this.buttonImage?.addEventListener('click', (e) => {
            e.preventDefault();
            this.editor.dispatchCommand(OPEN_IMAGE_SELECTOR_MODAL_COMMAND, {});
        });
    }

    addEvent(className: string, listener: (this: Element, ev: Event) => any) {
        this.root.querySelector('.jakon-editor-toolbar .' + className)?.addEventListener('click', listener);
    }

    formatHeading = (
        editor: LexicalEditor,
        headingSize: HeadingTagType,
    ) => {
        console.log("Format heading", headingSize);
        const blockType = ToolbarContext.getToolbarStoreForId(this.editorId).get().blockType;
        if (true || blockType !== headingSize) { // TODO
            editor.update(() => {
                $addUpdateTag(SKIP_SELECTION_FOCUS_TAG);
                const selection = $getSelection();
                $setBlocksType(selection, () => $createHeadingNode(headingSize));
            });
        }
    };

    private updateToolbarState(key: string, value: any) {
        switch (key) {
            case ToolbarContextKey.IS_BOLD:
                Utils.setActiveClass(this.buttonBold, value);
                break;
            case ToolbarContextKey.IS_ITALIC:
                Utils.setActiveClass(this.buttonItalic, value);
                break;
            case ToolbarContextKey.IS_STRIKETHROUGH:
                Utils.setActiveClass(this.buttonStrikethrough, value);
                break;
            case ToolbarContextKey.BLOCK_TYPE:
                this.setBlockTypeDropdown(value);
                break;
            case ToolbarContextKey.IS_LINK:
                Utils.setActiveClass(this.buttonLink, value);
                break;
        }
        ToolbarContext.getToolbarStoreForId(this.editorId).update({[key]: value});
    }

    private setBlockTypeDropdown(type: keyof typeof blockTypeToBlockName) {
        switch (type) {
            case "paragraph":
                this.dropdownBlockTypeButton!.innerText = "Normal";
                // TODO
                break;
            case "h1":
                this.dropdownBlockTypeButton!.innerText = "H1";
                this.dropdownBlockTypeH1?.classList.add('active');
                break;
            case "h2":
                this.dropdownBlockTypeButton!.innerText = "H2";
                this.dropdownBlockTypeH2?.classList.add('active');
                break;
            case "h3":
                this.dropdownBlockTypeButton!.innerText = "H3";
                this.dropdownBlockTypeH3?.classList.add('active');
                break;
        }
    }

    updateToolbar() {
        const selection = $getSelection();
        if (!selection) {
            this.updateToolbarState(ToolbarContextKey.SELECTION, false);
            return
        }
        this.updateToolbarState(ToolbarContextKey.SELECTION, true);
        if ($isRangeSelection(selection)) {
            /*if (activeEditor !== editor && $isEditorIsNestedEditor(activeEditor)) {
                const rootElement = activeEditor.getRootElement();
                this.updateToolbarState(
                    'isImageCaption',
                    !!rootElement?.parentElement?.classList.contains(
                        'image-caption-container',
                    ),
                );
            } else {
                this.updateToolbarState('isImageCaption', false);
            }*/

            const anchorNode = selection.anchor.getNode();
            const element = this.$findTopLevelElement(anchorNode);
            const elementKey = element.getKey();
            const elementDOM = this.editor.getElementByKey(elementKey);

            // Update links
            const node = Utils.getSelectedNode(selection);
            const parent = node.getParent();
            const isLink = $isLinkNode(parent) || $isLinkNode(node);
            this.updateToolbarState(ToolbarContextKey.IS_LINK, isLink);

            const tableNode = $findMatchingParent(node, $isTableNode);
            if ($isTableNode(tableNode)) {
                this.updateToolbarState('rootType', 'table');
            } else {
                this.updateToolbarState('rootType', 'root');
            }

            if (elementDOM !== null) {
                //setSelectedElementKey(elementKey);
                if ($isListNode(element)) {
                    const parentList = $getNearestNodeOfType<ListNode>(
                        anchorNode,
                        ListNode,
                    );
                    const type = parentList
                        ? parentList.getListType()
                        : element.getListType();

                    this.updateToolbarState(ToolbarContextKey.BLOCK_TYPE, type);
                } else {
                    this.$handleHeadingNode(element);
                    this.$handleCodeNode(element);
                }
            }

            // Handle buttons


            let matchingParent;
            if ($isLinkNode(parent)) {
                // If node is a link, we need to fetch the parent paragraph node to set format
                matchingParent = $findMatchingParent(
                    node,
                    (parentNode) => $isElementNode(parentNode) && !parentNode.isInline(),
                );
            }

            // If matchingParent is a valid node, pass it's format type
            this.updateToolbarState(
                'elementFormat',
                $isElementNode(matchingParent)
                    ? matchingParent.getFormatType()
                    : $isElementNode(node)
                        ? node.getFormatType()
                        : parent?.getFormatType() || 'left',
            );
        }
        if ($isRangeSelection(selection) || $isTableSelection(selection)) {
            // Update text format
            this.updateToolbarState(ToolbarContextKey.IS_BOLD, selection.hasFormat('bold'));
            this.updateToolbarState(ToolbarContextKey.IS_ITALIC, selection.hasFormat('italic'));
            this.updateToolbarState('isUnderline', selection.hasFormat('underline'));
            this.updateToolbarState(ToolbarContextKey.IS_STRIKETHROUGH, selection.hasFormat('strikethrough'),);
            this.updateToolbarState('isCode', selection.hasFormat('code'));
            this.updateToolbarState('isLowercase', selection.hasFormat('lowercase'));
            this.updateToolbarState('isUppercase', selection.hasFormat('uppercase'));
            this.updateToolbarState('isCapitalize', selection.hasFormat('capitalize'));
        }
        if ($isNodeSelection(selection)) {
            const nodes = selection.getNodes();
            for (const selectedNode of nodes) {
                const parentList = $getNearestNodeOfType<ListNode>(
                    selectedNode,
                    ListNode,
                );
                if (parentList) {
                    const type = parentList.getListType();
                    this.updateToolbarState(ToolbarContextKey.BLOCK_TYPE, type);
                } else {
                    const selectedElement = this.$findTopLevelElement(selectedNode);
                    this.$handleHeadingNode(selectedElement);
                    this.$handleCodeNode(selectedElement);
                    // Update elementFormat for node selection (e.g., images)
                    if ($isElementNode(selectedElement)) {
                        this.updateToolbarState(
                            'elementFormat',
                            selectedElement.getFormatType(),
                        );
                    }
                }
            }
        }
    }

    private $findTopLevelElement(node: LexicalNode) {
        let topLevelElement =
            node.getKey() === 'root'
                ? node
                : $findMatchingParent(node, (e) => {
                    const parent = e.getParent();
                    return parent !== null && $isRootOrShadowRoot(parent);
                });

        topLevelElement ??= node.getTopLevelElementOrThrow();
        return topLevelElement;
    }

    private $handleHeadingNode(selectedElement: LexicalNode) {
        const type = $isHeadingNode(selectedElement) ? selectedElement.getTag() : selectedElement.getType();
        if (type in blockTypeToBlockName) {
            this.updateToolbarState(ToolbarContextKey.BLOCK_TYPE, type as keyof typeof blockTypeToBlockName);
        }
    }

    private $handleCodeNode(element: LexicalNode) {
        const isCodeHighlighted = false;
        if ($isCodeNode(element)) {
            const language = element.getLanguage();
            this.updateToolbarState(
                'codeLanguage',
                language ? (isCodeHighlighted && normalizeCodeLanguagePrism(language)) || language : '',
            );
            const theme = element.getTheme();
            this.updateToolbarState('codeTheme', theme || '');
        }
    }
}

export function registerToolbarPlugin(instanceContext: InstanceContext): void {
    new ToolbarPlugin().init(instanceContext);
}