import {HeadingNode, QuoteNode, registerRichText} from "@lexical/rich-text";
import {$getRoot, createEditor} from "lexical";
import {mergeRegister} from "@lexical/utils";
import {createEmptyHistoryState, registerHistory} from "@lexical/history";
import {$convertFromMarkdownString, $convertToMarkdownString} from "@lexical/markdown";
import {registerActionsPlugin} from "./plugins/ActionsPlugin";
import {CodeHighlightNode, CodeNode, registerCodeHighlighting} from "@lexical/code";
import {registerToolbarPlugin} from "./plugins/ToolbarPlugin";
import Utils, {MARKDOWN_TRANSFORMERS} from "./Utils";
import editorTheme from "./theme/theme";
import {registerFloatingLinkEditorPlugin} from "@/plugins/FloatingLinkEditorPlugin";
import {InstanceContext} from "@/context/InstanceContext";
import {LinkNode} from "@lexical/link";
import {HorizontalRuleNode,} from '@lexical/extension';
import {registerHorizontalRulePlugin} from "@/plugins/HorizonalRulePlugin";
import {registerImageSelectorPlugin} from "@/plugins/ImageSelectorPlugin";
import {ImageNode} from "@/nodes/ImageNode";

export default class JakonEditor {

    textarea!: HTMLElement

    init(fieldHash: number) {
        console.log("Initializing editor for field: " + fieldHash);

        const wrapper = document.getElementById('jakon-editor-wrapper-' + fieldHash);
        this.textarea = document.getElementById('ta-' + fieldHash)!;
        const editorRef = document.getElementById('lexical-editor-' + fieldHash);

        if (!wrapper || !editorRef) {
            console.error("Editor elements not found");
            return;
        }

        const initialConfig = {
            namespace: 'Vanilla JS Demo',
            // Register nodes specific for @lexical/rich-text
            nodes: [HeadingNode, QuoteNode, CodeNode, CodeHighlightNode, LinkNode, HorizontalRuleNode, ImageNode],
            onError: (error: Error) => {
                throw error;
            },
            theme: editorTheme,
        };
        const editor = createEditor(initialConfig);
        editor.setRootElement(editorRef);

        // Registering Plugins
        mergeRegister(
            registerRichText(editor),
            registerHistory(editor, createEmptyHistoryState(), 300),
            registerCodeHighlighting(editor)
        );
        const instanceContext = new InstanceContext();
        instanceContext.wrapper = wrapper;
        instanceContext.fieldHash = fieldHash;
        instanceContext.editor = editor;
        instanceContext.floatingLinkEditor = registerFloatingLinkEditorPlugin(editor, wrapper, fieldHash);
        registerActionsPlugin(editor, wrapper);
        registerToolbarPlugin(instanceContext);
        registerHorizontalRulePlugin(instanceContext);
        registerImageSelectorPlugin(instanceContext);
        //registerMarkdownShortcuts(editor, TRANSFORMERS);
        Utils.instanceContext = instanceContext;

        editor.update(() => {
            $convertFromMarkdownString(this.textarea.textContent || '', MARKDOWN_TRANSFORMERS);
        });

        editor.registerUpdateListener(({editorState}) => {
            editorState.read(() => {
                this.updateTextarea();
            });
        });


        console.log("Done ts editor for field: " + fieldHash);
    }

    updateTextarea() {
        if (Utils.isMarkdownMode($getRoot())) {
            this.textarea.textContent = $getRoot().getTextContent();
        } else {
            this.textarea.textContent = $convertToMarkdownString(
                MARKDOWN_TRANSFORMERS,
                undefined,
                true,
            );
        }
    }
}

for (const el of document.querySelectorAll(".jakon-editor-wrapper")) {
    const editorId = (el as HTMLElement).dataset.editorid;
    new JakonEditor().init(Number(editorId));
}
