import {$createTextNode, $getRoot, LexicalEditor} from "lexical";
import {$createCodeNode} from '@lexical/code';
import {
    $convertFromMarkdownString,
    $convertToMarkdownString,
} from '@lexical/markdown';
import Utils, {MARKDOWN_TRANSFORMERS} from "../Utils";

export class Actions {

    shouldPreserveNewLinesInMarkdown = true;
    editor!: LexicalEditor

    init(editor: LexicalEditor, root: HTMLElement) {
        console.log("Initializing actions plugin", root);
        root.querySelector('.markdown-toggle')?.addEventListener('click', (e) => {
            console.log("Markdown toggle clicked");
            e.preventDefault();
            this.handleMarkdownToggle();
        })
        this.editor = editor;
    }

    handleMarkdownToggle() {
        this.editor.update(() => {
            const root = $getRoot();
            const firstChild = root.getFirstChild();
            if (Utils.isMarkdownMode(root)) {
                $convertFromMarkdownString(
                    firstChild?.getTextContent() || '',
                    MARKDOWN_TRANSFORMERS,
                    undefined, // node
                    this.shouldPreserveNewLinesInMarkdown,
                );
            } else {
                const markdown = $convertToMarkdownString(
                    MARKDOWN_TRANSFORMERS,
                    undefined, //node
                    this.shouldPreserveNewLinesInMarkdown,
                );
                const codeNode = $createCodeNode('markdown');
                codeNode.append($createTextNode(markdown));
                root.clear().append(codeNode);
                if (markdown.length === 0) {
                    codeNode.select();
                }
            }
        });
    }

}

export function registerActionsPlugin(editor: LexicalEditor, root: HTMLElement): void {
    new Actions().init(editor, root);
}
