import {
    $getNodeByKey, $insertNodes, COMMAND_PRIORITY_EDITOR,
    createCommand,
    LexicalEditor,
    NodeKey
} from "lexical";
import {InstanceContext} from "@/context/InstanceContext";
import Utils from "@/Utils";
import {$createImageNode} from "@/nodes/ImageNode";

export const OPEN_IMAGE_SELECTOR_MODAL_COMMAND = createCommand('OPEN_IMAGE_SELECTOR_MODAL_COMMAND');
export const DELETE_IMAGE_COMMAND = createCommand('DELETE_IMAGE_COMMAND');

export class ImageSelectorPlugin {

    root!: HTMLElement
    editor!: LexicalEditor
    editorId!: number

    modalHolder!: HTMLElement;
    apiUrl = "http://localhost:4566"
    endPoint = this.apiUrl + "/admin/api/images";

    init(instanceContext: InstanceContext) {
        console.log("Initializing image selector plugin", instanceContext.wrapper);
        this.root = instanceContext.wrapper;
        this.editor = instanceContext.editor;
        this.editorId = instanceContext.fieldHash;
        this.modalHolder = this.root.querySelector(".image_selector") as HTMLElement;

        this.editor.registerCommand(OPEN_IMAGE_SELECTOR_MODAL_COMMAND, (_) => {
            Utils.fetch(this.endPoint, {})
                .then((data) => this.openImageSelector(data))
                .catch(error => console.error(error));

            return true;
        }, COMMAND_PRIORITY_EDITOR);

        this.editor.registerCommand(DELETE_IMAGE_COMMAND, (key: NodeKey) => {
            this.editor.update(() => {
                const imageNode = $getNodeByKey(key);
                if (imageNode) {
                   imageNode.remove();
                }
            });
            return true;
        }, COMMAND_PRIORITY_EDITOR);
    }


    private openImageSelector(data: any) {
        console.log(data);
        this.modalHolder.innerHTML = `
			<div id="image_selector_modal-${this.editorId}" class="modal" tabindex="-1" role="dialog">
			  <div class="modal-dialog modal-lg" role="document">
				<div class="modal-content">
				  <div class="modal-header">
					<button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
					<h4 class="modal-title" id="gridSystemModalLabel">Aktuln sloka: upload/</h4>
				  </div>
				  <div class="modal-body">
				  </div>
				</div>
			  </div>
			</div>
        `;
        // @ts-ignore
        const modal = new bootstrap.Modal(document.getElementById('image_selector_modal-' + this.editorId), {});
        this.modalHolder.querySelector(".close")?.addEventListener("click", () => {
            this.close(modal);
        });

        const target = this.modalHolder.querySelector(".modal-body");
        if (target) {
            this.createList(data, target, modal);
        }

        // @ts-ignore
        modal.show();
        return true;
    }

    private createList(data: any, target: Element, modal: any) {
        data.result.forEach((f: { fileType: string; id: string; name: string; path: string; }) => {
            let icon;
            if (f.fileType === "IMAGE") {
                icon = "fa-image";
            } else if (f.fileType === "FOLDER") {
                icon = "fa-folder";
            }

            const btn = f.fileType === "IMAGE" ? `<button class="btn btn-primary btn-sm">Vloit</button>` : "";
            const html = `
				<div class="row" id="fid${f.id}">
					<div class="col-md-10">
						<i class="far ${icon}"></i><span>${f.name}</span>
					</div>
					<div class="col-md-2">
						${btn}
					</div>
				</div>`

            target.insertAdjacentHTML("beforeend", html);

            if (f.fileType === "IMAGE") {
                const select = target.querySelector("#fid" + f.id + " button");
                select?.addEventListener("click", e => {
                    e.preventDefault();
                    this.fillImage(f.name, this.apiUrl + "/" + f.path + f.name, modal);
                });
            } else if (f.fileType === "FOLDER") {
                const row = target.querySelector("#fid" + f.id);
                row?.classList.add("pointer");
                row?.addEventListener("click", e => {
                    e.preventDefault();
                    this.changeFolder(f.path + f.name, modal);
                });
            }
        });
    }

    private changeFolder(path: string, modal: any) {
        Utils.fetch(this.endPoint, {
            path : path
        })
            .then((data) => {
                console.log(data);
                const target = this.modalHolder.querySelector(".modal-body");
                if (target) {
                    target.innerHTML = "";
                    this.createList(data, target, modal);
                }

                const parentPath = path.substring(0, path.lastIndexOf("/"));
                const hasParent = parentPath !== "";
                const parentArrow = hasParent ? `<i class="fas fa-arrow-up pointer"></i>` : "";

                const modalTitle = this.modalHolder.querySelector(".modal-title");
                if (modalTitle) {
                    modalTitle.innerHTML = `<h4 class="modal-title" id="gridSystemModalLabel">${parentArrow} Aktuln sloka: ${path}</h4>`;
                }

                if (hasParent) {
                    const goUpIcon = this.modalHolder.querySelector(".modal-title i");
                    if (goUpIcon) {
                        goUpIcon.addEventListener("click", (e) => {
                            this.changeFolder(path.substring(0, path.lastIndexOf("/")), modal);
                        });
                    }
                }
            })
            .catch(error => console.error(error));
    }

    private fillImage(name: string, link: string, modal: any) {
        this.editor.update(() => {
            console.log("Inserting image: ", link, name);
            const imageNode = $createImageNode(link, name);
            $insertNodes([imageNode]);
        });
        this.close(modal);
    }

    private close(modal: any) {
        modal.hide();
        this.modalHolder.innerHTML = "";
    }
}

export function registerImageSelectorPlugin(instanceContext: InstanceContext): ImageSelectorPlugin {
    const plugin = new ImageSelectorPlugin();
    plugin.init(instanceContext);
    return plugin;
}