import {$getSelection, $isRangeSelection, COMMAND_PRIORITY_EDITOR, COMMAND_PRIORITY_LOW, createCommand, LexicalEditor} from "lexical";
import {$toggleLink,} from "@lexical/link";
import Utils from "@/Utils";

export const INSERT_LINK_URL_COMMAND = createCommand('INSERT_LINK_URL_COMMAND')
export const OPEN_LINK_MODAL_COMMAND = createCommand('OPEN_LINK_MODAL_COMMAND')
export const REMOVE_LINK_MODAL_COMMAND = createCommand('REMOVE_LINK_MODAL_COMMAND')

export class FloatingLinkEditorPlugin {

    root!: HTMLElement
    editor!: LexicalEditor
    editorId!: number
    modalInstance: any

    linkTextInput!: HTMLInputElement
    linkUrlInput!: HTMLInputElement

    init(editor: LexicalEditor, root: HTMLElement, editorId: number) {
        console.log("Initializing floating link editor plugin", root);
        this.root = root;
        this.editor = editor;
        this.editorId = editorId;

        this.linkTextInput = this.root.querySelector('.linkText')!;
        this.linkUrlInput = this.root.querySelector('.linkUrl')!;

        this.root.querySelector('.save-link-' + this.editorId)?.addEventListener('click', () => {
            this.handleLinkSubmission();
        });
        this.root.querySelector('.remove-link-' + this.editorId)!.addEventListener('click', () => {
            this.editor.dispatchCommand(REMOVE_LINK_MODAL_COMMAND, {});
        });

        editor.registerCommand(INSERT_LINK_URL_COMMAND, (payload) => {
            return this.insertLink(payload);
        }, COMMAND_PRIORITY_LOW);
        editor.registerCommand(OPEN_LINK_MODAL_COMMAND, (payload) => {
            return this.openLinkEditor(payload);
        }, COMMAND_PRIORITY_EDITOR);
        editor.registerCommand(REMOVE_LINK_MODAL_COMMAND, () => {
            return this.removeLink();
        }, COMMAND_PRIORITY_LOW);
    }


    openLinkEditor(payload: any) {
        const linkEditMode = payload || false;
        // @ts-ignore
        const modal = new bootstrap.Modal(document.getElementById('linkEditorModal-' + this.editorId), {});
        this.modalInstance = modal;
        console.log("Opening link editor modal, edit mode: " + linkEditMode);
        const selection = $getSelection();
        if (linkEditMode) {
            // In edit mode, try to get the link url from the selection
            if ($isRangeSelection(selection)) {
                const anchorNode = selection.anchor.getNode();
                let linkNode = null;
                if (anchorNode.getParent() && anchorNode.getParent()?.__type === 'link') {
                    linkNode = anchorNode.getParent();
                } else if (anchorNode.__type === 'link') {
                    linkNode = anchorNode;
                }
                if (linkNode) {
                    // @ts-ignore
                    this.linkUrlInput.value = linkNode.getURL();
                    this.linkTextInput.value = linkNode.getTextContent();
                }
            }
        } else {
            this.linkTextInput.value = $isRangeSelection(selection) ? selection.getTextContent() : "";
            this.linkUrlInput.value = 'https://';
        }
        // @ts-ignore
        modal.show();
        return true;
    }

    handleLinkSubmission() {
        const linkUrl = this.linkUrlInput.value;
        if (linkUrl !== '') {
            const link = {linkUrl: Utils.sanitizeUrl(linkUrl), linkType: 'normal'};
            console.log("Inserting link: ", link);
            this.editor.dispatchCommand(
                INSERT_LINK_URL_COMMAND,
                link,
            );
        }

        this.modalInstance.hide();
    }

    insertLink(payload: any) {
        const { linkUrl, linkType } = payload

        if (linkUrl && linkUrl !== '') {
            this.editor.update(() => {
                const selection = $getSelection();
                const linkText = this.linkTextInput.value === "" ? linkUrl : this.linkTextInput.value;
                if ($isRangeSelection(selection)) {
                    // Pokusme se najt parent link node
                    const anchorNode = selection.anchor.getNode();
                    let linkNode = null;

                    if (anchorNode.getParent() && anchorNode.getParent()?.__type === 'link') {
                        linkNode = anchorNode.getParent();
                    } else if (anchorNode.__type === 'link') {
                        linkNode = anchorNode;
                    }

                    if (linkNode) {
                        // Mme existujc link - smaeme ho a vlome nov
                        linkNode.remove();
                        selection.insertText(linkText);

                        // Vybereme vloen text
                        const {anchor, focus} = selection;
                        anchor.offset -= linkText.length;
                        focus.offset = anchor.offset + linkText.length;

                        $toggleLink(linkUrl, { target: linkType});
                    } else {
                        // Nov link - pvodn logika
                        selection.removeText();
                        selection.insertText(linkText);

                        const {anchor, focus} = selection;
                        anchor.offset -= linkText.length;
                        focus.offset = anchor.offset + linkText.length;

                        $toggleLink(linkUrl, { target: linkType});
                    }
                }
                this.linkTextInput.value = '';
                this.linkUrlInput.value = 'https://';
            });
        }

        //clean up
        return true
    }

    removeLink() {
        this.editor.update(() => {
            const selection = $getSelection();
            if ($isRangeSelection(selection)) {
                const anchorNode = selection.anchor.getNode();
                let linkNode = null;

                if (anchorNode.getParent() && anchorNode.getParent()?.__type === 'link') {
                    linkNode = anchorNode.getParent();
                } else if (anchorNode.__type === 'link') {
                    linkNode = anchorNode;
                }

                if (linkNode) {
                    // Zskme text z odkazu
                    const linkText = linkNode.getTextContent();
                    // Smaeme link node
                    linkNode.remove();
                    // Vlome ist text zpt
                    selection.insertText(linkText);
                }
            }
        });
        this.modalInstance.hide();
        return true;
    }

}

export function registerFloatingLinkEditorPlugin(editor: LexicalEditor, root: HTMLElement, editorId: number): FloatingLinkEditorPlugin {
    const plugin = new FloatingLinkEditorPlugin();
    plugin.init(editor, root, editorId);
    return plugin;
}