import {ElementNode, LexicalNode, RangeSelection, RootNode, TextNode} from "lexical";
import {$isCodeNode} from "@lexical/code";
import {$isAtNodeEnd} from '@lexical/selection';
import {ElementTransformer, TextMatchTransformer, TRANSFORMERS} from "@lexical/markdown";
import {$createHorizontalRuleNode, $isHorizontalRuleNode, HorizontalRuleNode} from "@lexical/extension";
import {$createImageNode, $isImageNode, ImageNode} from "@/nodes/ImageNode";
import {InstanceContext} from "@/context/InstanceContext";

export default class Utils {

    static instanceContext: InstanceContext;

    static isMarkdownMode(root: RootNode): boolean {
        const firstChild = root.getFirstChild();
        return $isCodeNode(firstChild) && firstChild.getLanguage() === 'markdown';
    }

    static  getSelectedNode(selection: RangeSelection,): TextNode | ElementNode {
        const anchor = selection.anchor;
        const focus = selection.focus;
        const anchorNode = selection.anchor.getNode();
        const focusNode = selection.focus.getNode();
        if (anchorNode === focusNode) {
            return anchorNode;
        }
        const isBackward = selection.isBackward();
        if (isBackward) {
            return $isAtNodeEnd(focus) ? anchorNode : focusNode;
        } else {
            return $isAtNodeEnd(anchor) ? anchorNode : focusNode;
        }
    }

    private static readonly SUPPORTED_URL_PROTOCOLS = new Set([
        'http:',
        'https:',
        'mailto:',
        'sms:',
        'tel:',
    ]);

    static sanitizeUrl(url: string): string {
        try {
            const parsedUrl = new URL(url);
            if (!this.SUPPORTED_URL_PROTOCOLS.has(parsedUrl.protocol)) {
                return 'about:blank';
            }
        } catch {
            return url;
        }
        return url;
    }

    static setActiveClass(element: HTMLElement | null, isActive: boolean) {
        if (!element) return;
        if (isActive) {
            element.classList.add('active');
        } else {
            element.classList.remove('active');
        }
    }

    static async fetch(url: string, data: any) {
        const response = await fetch(url, {
            body: JSON.stringify(data),
            headers: {
                'content-type': 'application/json'
            },
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // *client, no-referrer
        });
        if (response.ok) {
            return response.json();
        } else {
            throw new Error('Network response was not ok.');
        }
    }

}

export const HR: ElementTransformer = {
    dependencies: [HorizontalRuleNode],
    export: (node: LexicalNode) => {
        return $isHorizontalRuleNode(node) ? '***' : null;
    },
    regExp: /^(---|\*\*\*|___)\s?$/,
    replace: (parentNode, _1, _2, isImport) => {
        const line = $createHorizontalRuleNode();
        if (isImport || parentNode.getNextSibling() != null) {
            parentNode.replace(line);
        } else {
            parentNode.insertBefore(line);
        }

        line.selectNext();
    },
    type: 'element',
};
export const IMAGE: TextMatchTransformer = {
    dependencies: [ImageNode],
    export: (node) => {
        if (!$isImageNode(node)) {
            return null;
        }

        return `![${node.getAlt}](${node.getSrc})`;
    },
    importRegExp: /!(?:\[([^[]*)\])(?:\(([^(]+)\))/,
    regExp: /!(?:\[([^[]*)\])(?:\(([^(]+)\))$/,
    replace: (textNode, match) => {
        const [, altText, src] = match;
        const imageNode = $createImageNode(src, altText);
        console.log('Replacing markdown image with ImageNode:', altText, src, imageNode);
        textNode.replace(imageNode);
    },
    trigger: ')',
    type: 'text-match',
};
export const MARKDOWN_TRANSFORMERS = [IMAGE, ...TRANSFORMERS, HR];

